import request from "../../api"
import {
    COMMENT_VIDEO_FAIL,
    COMMENT_VIDEO_REQUEST,
    COMMENT_VIDEO_SUCCESS,
    CREATE_COMMENT_FAIL,
    CREATE_COMMENT_SUCCESS
} from "../reducers/actionType"

export const getCommentByVideo = (id) => async (dispatch) => {
    try {
        dispatch({
            type: COMMENT_VIDEO_REQUEST
        })

        const { data } = await request('/commentThreads', {
            params: {
                part: 'snippet',
                videoId: id
            }
        })


        dispatch({
            type: COMMENT_VIDEO_SUCCESS,
            payload: data.items
        })
    } catch (error) {
        dispatch({
            type: COMMENT_VIDEO_FAIL,
            payload: error.message,
        })
    }
}

export const addComment = (id, text) => async (dispatch, getState) => {
    try {
        const obj = {
            snippet: {
                videoId: id,
                topLevelComment: {
                    snippet: {
                        textOriginal: text,
                    }
                }
            }
        }

        await request.post('/commentThreads', obj, {
            params: {
                part: 'snippet',
            },
            headers: {
                Authorization: `Bearer ${getState().auth.accessToken}`,
            }
        })

        dispatch({
            type: CREATE_COMMENT_SUCCESS,
        })

        setTimeout(() => dispatch(getCommentByVideo(id)), 3000)

    } catch (error) {
        dispatch({
            type: CREATE_COMMENT_FAIL,
            payload: error.message
        })
    }
}